<template>
  <section class="loginContainer">
    <div class="loginInner">
      <div class="login_header">
        <h2 class="login_logo"></h2>
        <div class="login_header_title">
          <a href="javascript:;" :class="{on: loginWay}" @click="loginWay=true"></a>
          <a href="javascript:;" :class="{on: !loginWay}" @click="loginWay=false"></a>
        </div>
      </div>
      <div class="login_content">
        <form @submit.prevent="login">
          <div :class="{on: loginWay}">
            <section class="login_message">
              <input type="tel" maxlength="11" placeholder="" v-model="phone">
              <button :disabled="!rightPhone" class="get_verification" :class="{right_phone: rightPhone}" @click.prevent="getCode">
                {{computeTime>0 ? `(${computeTime}s)` : ''}}
              </button>
            </section>
            <section class="login_verification">
              <input type="tel" maxlength="8" placeholder="" v-model="code">
            </section>
            <section class="login_hint">
              
              <a href="javascript:;"></a>
            </section>
          </div>
          <div :class="{on: !loginWay}">
            <section>
              <section class="login_message">
                <input type="text" maxlength="11" placeholder="//" v-model="name">
              </section>
              <section class="login_verification">
                <input type="text" maxlength="8" placeholder="" v-if="showPwd" v-model="pwd">
                <input type="password" maxlength="8" placeholder="" v-else v-model="pwd">
                <div class="switch_button" :class="showPwd?'on':'off'" @click="showPwd=!showPwd">
                  <div class="switch_circle" :class="{right: showPwd}"></div>
                  <span class="switch_text">{{showPwd ? 'abc' : '...'}}</span>
                </div>
              </section>
              <section class="login_message">
                <input type="text" maxlength="11" placeholder="" v-model="captcha">
                <img class="get_verification" src="./images/captcha.svg" alt="captcha" @click="getCaptcha" ref="captcha">
              </section>
            </section>
          </div>
          <button class="login_submit"></button>
        </form>
        <a href="javascript:;" class="about_us"></a>
      </div>
      <a href="javascript:" class="go_back" @click="$router.back()">
        <i class="iconfont icon-jiantou2"></i>
      </a>
    </div>

    <AlertTip :alertText="alertText" v-show="alertShow" @closeTip="closeTip" />
  </section>
</template>

<script>
import AlertTip from '../../components/common/alertTip/AlertTip.vue'

export default {
  name: 'Login',
  components: { AlertTip },
  data () {
    return {
      loginWay: true, // true, false
      computeTime: 0, // 
      showPwd: false, // 
      phone: '', // 
      code: '', // 
      name: '', // 
      pwd: '', // 
      captcha: '', // 
      alertText: '', // 
      alertShow: false, // 
    }
  },
  computed: {
    rightPhone () {
      return /^1\d{10}$/.test(this.phone)
    }
  },

  methods: {
    // 
    async getCode () {
      // 
      if (!this.computeTime) {
        // 
        this.computeTime = 30
        this.intervalId = setInterval(() => {
          this.computeTime--
          if (this.computeTime <= 0) {
            // 
            clearInterval(this.intervalId)
          }
        }, 1000)

        // ajax()
        // const result = await reqSendCode(this.phone)
        // if (result.code === 1) {
        //   // 
        //   this.showAlert(result.msg)
        //   // 
        //   if (this.computeTime) {
        //     this.computeTime = 0
        //     clearInterval(this.intervalId)
        //     this.intervalId = undefined
        //   }
        // }
      }



    },

    showAlert (alertText) {
      this.alertShow = true
      this.alertText = alertText
    },
    // 
    async login () {
      let result
      // 
      if (this.loginWay) {  // 
        const { rightPhone, phone, code } = this
        if (!this.rightPhone) {
          // 
          this.showAlert('')
          return
        } else if (!/^\d{6}$/.test(code)) {
          // 6
          this.showAlert('6')
          return
        }
        // ajax
        // result = await reqSmsLogin(phone, code)

      } else {// 
        const { name, pwd, captcha } = this
        if (!this.name) {
          // 
          this.showAlert('')
          return
        } else if (!this.pwd) {
          // 
          this.showAlert('')
          return
        } else if (!this.captcha) {
          // 
          this.showAlert('')
          return
        }
        // ajax
        // result = await reqPwdLogin({ name, pwd, captcha })
      }

      // 
      if (this.computeTime) {
        this.computeTime = 0
        clearInterval(this.intervalId)
        this.intervalId = undefined
      }

      // 
      // if (result.code === 0) {
      //   const user = result.data
      //   // uservuexstate
      //   this.$store.dispatch('recordUser', user)
      //   // 
      //   this.$router.replace('/profile')
      // } else {
      //   // 
      //   this.getCaptcha()
      //   // 
      //   const msg = result.msg
      //   this.showAlert(msg)
      // }
    },
    // 
    closeTip () {
      this.alertShow = false
      this.alertText = ''
    },
    // 
    getCaptcha () {
      // src
      // this.$refs.captcha.src = 'http://localhost:4000/captcha?time=' + Date.now()
    },

  },

}
</script>

<style scoped>
.loginContainer {
  width: 100%;
  height: 100%;
  background: #fff;
}
.loginContainer .loginInner {
  padding-top: 60px;
  width: 80%;
  margin: 0 auto;
}
.loginContainer .loginInner .login_header .login_logo {
  font-size: 40px;
  font-weight: bold;
  color: #fd6020;
  text-align: center;
}
.loginContainer .loginInner .login_header .login_header_title {
  padding-top: 40px;
  text-align: center;
}
.loginContainer .loginInner .login_header .login_header_title > a {
  color: #333;
  font-size: 14px;
  padding-bottom: 4px;
}
.loginContainer .loginInner .login_header .login_header_title > a:first-child {
  margin-right: 40px;
}
.loginContainer .loginInner .login_header .login_header_title > a.on {
  color: #fd6020;
  font-weight: 700;
  border-bottom: 2px solid #02a774;
}
.loginContainer .loginInner .login_content > form > div {
  display: none;
}
.loginContainer .loginInner .login_content > form > div.on {
  display: block;
}
.loginContainer .loginInner .login_content > form > div input {
  width: 100%;
  height: 100%;
  padding-left: 10px;
  box-sizing: border-box;
  border: 1px solid #ddd;
  border-radius: 4px;
  outline: 0;
  font: 400 14px Arial;
}
.loginContainer .loginInner .login_content > form > div input:focus {
  border: 1px solid #02a774;
}
.loginContainer .loginInner .login_content > form > div .login_message {
  position: relative;
  margin-top: 16px;
  height: 48px;
  font-size: 14px;
  background: #fff;
}
.loginContainer
  .loginInner
  .login_content
  > form
  > div
  .login_message
  .get_verification {
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
  border: 0;
  color: #ccc;
  font-size: 14px;
  background: transparent;
}
.loginContainer
  .loginInner
  .login_content
  > form
  > div
  .login_message
  .get_verification.right_phone {
  color: #000;
}
.loginContainer .loginInner .login_content > form > div .login_verification {
  position: relative;
  margin-top: 16px;
  height: 48px;
  font-size: 14px;
  background: #fff;
}
.loginContainer
  .loginInner
  .login_content
  > form
  > div
  .login_verification
  .switch_button {
  font-size: 12px;
  border: 1px solid #ddd;
  border-radius: 8px;
  transition: background-color 0.3s, border-color 0.3s;
  padding: 0 6px;
  width: 30px;
  height: 16px;
  line-height: 16px;
  color: #fff;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
}
.loginContainer
  .loginInner
  .login_content
  > form
  > div
  .login_verification
  .switch_button.off {
  background: #fff;
}
.loginContainer
  .loginInner
  .login_content
  > form
  > div
  .login_verification
  .switch_button.off
  .switch_text {
  float: right;
  color: #ddd;
}
.loginContainer
  .loginInner
  .login_content
  > form
  > div
  .login_verification
  .switch_button.on {
  background: #02a774;
}
.loginContainer
  .loginInner
  .login_content
  > form
  > div
  .login_verification
  .switch_button
  > .switch_circle {
  position: absolute;
  top: -1px;
  left: -1px;
  width: 16px;
  height: 16px;
  border: 1px solid #ddd;
  border-radius: 50%;
  background: #fff;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
  transition: transform 0.3s;
}
.loginContainer
  .loginInner
  .login_content
  > form
  > div
  .login_verification
  .switch_button
  > .switch_circle.right {
  transform: translateX(30px);
}
.loginContainer .loginInner .login_content > form > div .login_hint {
  margin-top: 12px;
  color: #999;
  font-size: 14px;
  line-height: 20px;
}
.loginContainer .loginInner .login_content > form > div .login_hint > a {
  color: #02a774;
}
.loginContainer .loginInner .login_content > form .login_submit {
  display: block;
  width: 100%;
  height: 42px;
  margin-top: 30px;
  border-radius: 4px;
  background: #fd6020;
  color: #fff;
  text-align: center;
  font-size: 16px;
  line-height: 42px;
  border: 0;
}
.loginContainer .loginInner .login_content .about_us {
  display: block;
  font-size: 12px;
  margin-top: 20px;
  text-align: center;
  color: #999;
}
.loginContainer .loginInner .go_back {
  position: absolute;
  top: 5px;
  left: 5px;
  width: 30px;
  height: 30px;
}
.loginContainer .loginInner .go_back > .iconfont {
  font-size: 20px;
  color: #999;
}
</style>